}/* Make clicks pass-through */

#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #29d;
  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;
  width: 100%;
  height: 0.026667rem;
}

/* Fancy blur effect */

#nprogress .peg {
  display: block;
  position: absolute;
  right: 0;
  width: 1.333333rem;
  height: 100%;
  -webkit-box-shadow: 0 0 0.133333rem #29d, 0 0 0.066667rem #29d;
  box-shadow: 0 0 0.133333rem #29d, 0 0 0.066667rem #29d;
  opacity: 1.0;
  -webkit-transform: rotate(3deg) translate(0, -0.053333rem);
  transform: rotate(3deg) translate(0, -0.053333rem);
}

/* Remove these to get rid of the spinner */

#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 0.2rem;
  right: 0.2rem;
}

#nprogress .spinner-icon {
  width: 0.24rem;
  height: 0.24rem;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  border: solid 0.026667rem transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 50%;
  -webkit-animation: nprogress-spinner 400ms linear infinite;
  animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}

@-webkit-keyframes nprogress-spinner {
  0% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes nprogress-spinner {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}